import axios from "axios";
export const CLICKHOUSE_CONFIG = {
    protocol: 'http',
    host: 'localhost',
    port: 8123,
    path: ''
};
export function getCHConfig() {
    return CLICKHOUSE_CONFIG;
}
export function setCHConfig(config) {
    Object.keys(config).forEach(k => {
        CLICKHOUSE_CONFIG[k] = config[k];
    });
}
export class CHUtils {
    constructor(config) {
        this.config = config || {
            protocol: 'http',
            host: 'localhost',
            port: 8123,
            path: ''
        };
    }
    getCHConfig() {
        return this.config;
    }
    setCHConfig(config) {
        Object.keys(config).forEach(k => {
            this.config[k] = config[k];
        });
    }
    async query(sql) {
        try {
            const config = this.config;
            const res = await axios(`${config.protocol}://${config.host}:${config.port}${config.path}?query=${sql}`);
            return res.data;
        }
        catch (error) {
            throw new Error(`[SQL Error] ${sql}.\n${error.toString()}`);
        }
    }
    async queryw(sql) {
        try {
            const config = this.config;
            const res = await axios({
                url: `${config.protocol}://${config.host}:${config.port}${config.path}`,
                method: 'post',
                params: {
                    query: sql
                }
            });
            return res.data;
        }
        catch (error) {
            throw new Error(`[SQL Error] ${sql}.\n${error.toString()}`);
        }
    }
}
